								<button id="save" className="btn btn-warning btn-lg buttons" type="submit" onClick={this.handleSaveOrUpdate}>Save</button>
								{this.props.student &&
									<button id="delete" className="btn btn-warning btn-lg buttons" type="submit" onClick={this.handleDelete}>Delete</button>}
								<button id="close" className="btn btn-warning btn-lg buttons" type="submit" onClick={this.handleClose}>Close</button>
